import Navbar from '@/components/Navbar'
import ShopPage from '@/sections/shop/ShopPage'
import ShowProducts from '@/sections/shop/ShowProduct'
import { useState } from 'react' 
import Popover from '@/components/Popover'
import React from 'react'

const ProductPage = () => {
  const [isPopoverOpen, setIsPopoverOpen] = useState(false);

  const handleOpenPopover = () => setIsPopoverOpen(true);
  const handleClosePopover = () => setIsPopoverOpen(false);
  return (
    <div>
       <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100">
      <button
        onClick={handleOpenPopover}
        className="bg-blue-500 text-white py-2 px-4 rounded-lg hover:bg-blue-600 transition"
      >
        Open Popover
      </button>
      <Popover isOpen={isPopoverOpen} onClose={handleClosePopover}>
        <h2 className="text-xl font-bold text-center mb-4">Popover Content</h2>
        <p className="text-gray-700 text-center">
          This can contain any content, including another page.
        </p>
      </Popover>
    </div>
      {/* <Navbar />
      <ShopPage /> */}
    </div>
  )
}

export default ProductPage
